<template>
  <div class="guide container">
    <div class="nav-bread">{{ $t("navBread.des") }}: > <router-link to="/"> {{ $t("navBread.index") }} </router-link> > <router-link to="/trade"> {{ $t("navBread.fabiTrade") }} </router-link>> <a  @click="handleGuideRouter" href="javascript:void(0)"> {{ $t("navBread.announcement") }} </a> > <a href="javascript:void(0)"> {{ navName }} </a></div>
    <div class="clearfix">
      <div class="navleft pull-left">
        <navleft :navItem="navItem" :navObj="navObj" @getArticleListContent="getArticleListContent"></navleft>
      </div>
      <div class="r-content pull-right" >
        <div id="notice-title" class="notice-title" v-html="navName">
        </div>
        <!--  -->
         <div id="notice-content">
            <ul class="notice-content">
              <li class="clearfix" @click="handleToDetail(item.id)" v-for="item in contentList" :key="item.id"><div class="pull-left"><img :src="item.image_path" alt="" class="notice-img"></div><div class="pull-right notice-detail"><div class="clearfix"><h4 class="pull-left">{{ item.title }}</h4><div class="pull-right notice-detail-from">来源：{{ item.source }}  <span class="time">时间：  {{ item.create_time }}</span></div></div><div class="notice-detail-content"> {{ item.content }}</div></div></li>
            </ul>
            <div class="link-page" v-if="contentList.length > 10">
              <el-button type="primary" :disabled="previousDisable" @click="handleToPre"><span class="el-icon-arrow-left"></span></el-button>
              <el-button type="primary" :disabled="nextDisable" @click="handleToNext"><span class="el-icon-arrow-right"></span></el-button>
            </div>
         </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { getCates, getArticleList } from '@/api/notice'
  import navleft from './components/leftNavChange'
  export default {
    components: { navleft },
    data() {
      return {
        navItem: '',
        navName: '',
        navObj: [],
        title: '',
        id: 0,
        contentList: [],
        pageOptions: {
          per_page_num: 10,
          page: 1
        },
        previousDisable: true,
        nextDisable: true,
        totalCount: 0
      }
    },
    created() {
      this.init()
    },
    methods: {
      init() {
        getCates().then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.navObj = [...res.data]
          if (this.navObj.length > 0) {
            this.navItem = res.data[0].name
            this.navName = res.data[0].name
            this.getArticleListContent(this.navObj[0].id, this.navName)
          }
        }).catch(() => {
  
        })
      },
      handleToPre() {
        if ((this.pageOptions.page - 1) >= 1) {
          this.pageOptions.page = this.pageOptions.page--
          this.getArticleListContentPage(this.id)
          this.nextDisable = false
          if (this.pageOptions.page === 1) {
            this.previousDisable = true
          }
        }
      },
      handleToNext() {
        if ((this.pageOptions.page + 1) < parseInt(this.totalCount / this.pageOptions.per_page_num)) {
          this.pageOptions.page = this.pageOptions.page++
          this.getArticleListContentPage(this.id)
          this.previousDisable = false
          if (this.pageOptions.page === parseInt(this.totalCount / this.pageOptions.per_page_num)) {
            this.nextDisable = true
          }
        }
      },
      getArticleListContent(id, name) {
        const data = { ...this.pageOptions }
        data.cate_id = id
        getArticleList(data).then(res => {
          console.log(res)
          this.navName = name
          if (res.status !== 1000) {
            return false
          }
          this.id = id
          this.contentList = res.data.article_list
          this.totalCount = res.data.page_count
          if (this.totalCount > this.pageOptions.per_page_num) {
            this.nextDisable = false
            this.previousDisable = true
          } else {
            this.nextDisable = true
            this.previousDisable = true
          }
        }).catch(() => {
  
        })
      },
      getArticleListContentPage(id) {
        const data = { ...this.pageOptions }
        data.cate_id = id
        getArticleList(data).then(res => {
          console.log(res)
          if (res.status !== 1000) {
            return false
          }
          this.contentList = res.data.article_list
        }).catch(() => {
  
        })
      },
      handleGuideRouter() {
        window.location.reload()
      },
      handleToDetail(id) {
        this.$router.push({ name: 'articleDetail', query: { id: id }})
      }
    }
  }
</script>
<style lang="less" scoped>
  .r-content{
    background: white;
    min-height: 770px;
    padding: 20px 25px;
    width: 917px !important;
    .notice-title{
      padding-top: 9px;
      padding-bottom: 18px;
      border-bottom: 1px solid #e6eaf0;
      font-size: 16px;
    }
    ul li {
      padding: 13px 0 15px;
      cursor: pointer;
      border-bottom: 1px solid #F5F5F5;
    }
    .notice-detail {
      width: 785px;
      color: #787878;
      padding: 6px 0 15px;
      font-size: 13px;
    }
    .notice-detail h4 {
      color: #3e4558;
      font-size: 16px;
      font-weight: normal;
    }
    .notice-img {
      width: 100px;
      height: 100px;
    }
    .link-page{
      text-align: right;
      padding-top: 20px;
    }
    .link-page{
      /deep/ button{
        padding: 7px;
      }
    }
    .content img{
      width: 876px !important;
      height: auto;
      border: 1px solid #F5F5F5;
    }
    .notice-detail .notice-detail-from {
      padding: 10px;
    }

  }
</style>


