<template>
  <div class="nav guide-nav">
   <el-row class="tac">
    <el-col :span="24">
      <el-menu
        :default-active="navItem"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        :router=false
        @close="handleClose">
        <el-submenu :index="item.name"  v-for="item in navObj" :key="item.id">
          <template slot="title" >
            <span @click="$emit('getArticleListContent', item.id, item.name)">{{ item.name }}</span>
          </template>
          <el-submenu :index="item1.name"  v-for="item1 in item.child" :key="item1.id">
            <template slot="title" ><span @click="$emit('getArticleListContent', item1.id, item1.name)">{{ item1.name }}</span></template>
            <el-menu-item :index="item2.name"  v-for="item2 in item1.child" :key="item2.id" @click="$emit('getArticleListContent', item2.id, item2.name)">{{ item2.name }}</el-menu-item>
          </el-submenu>
        </el-submenu>

        <!-- <el-menu-item :index="item.name"  v-for="item in navObj" :key="item.id" @click="$emit('getArticleListContent', item.id, item.name)"> -->
          <!-- <i class="el-icon-menu"></i> -->
          <!-- <span>{{ item.name }}</span> -->
        <!-- </el-menu-item> -->
      </el-menu>
    </el-col>
  </el-row>
  </div>
</template>
<script>
  export default {
    props: ['navItem', 'navObj'],
    methods: {
      handleOpen(key, keyPath) {
        // console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        // console.log(key, keyPath);
      }
    }
  }
</script>

<style lang="less">
  .guide-nav{
    .nav-icon{
      width: 20px;
      height: 20px;
    }
    .el-menu-item{
      text-align: center;
    }
  }
</style>



